<template>
    <div>
        <Head></Head>
        <div class="main">
            <div class="register">
                <div class="from">
                    <h3></h3>
                    <el-input v-model="name" placeholder="" size="small" maxlength="12"></el-input>
                    <el-input v-model="password" placeholder="" size="small" maxlength="9" type="password"></el-input>
                    <el-input v-model="email" placeholder="" size="small"></el-input>
                    <el-input v-model="tel" placeholder="" size="small" maxlength="11"></el-input>
                    <el-button size="small" @click="register"></el-button>
                </div>
            </div>
        </div>
        <Footer></Footer>
    </div>
</template>

<script>
    import Head from "./subassembly/Head"
    import Footer from "./subassembly/Footer"

    export default {
        name: "Register",
        components:{
            Head,Footer
        },
        data:function () {
            return {
                name:"",
                password:"",
                email:"",
                tel:"",
                balance:0,
                min:0
            }
        },
        methods:{
            register:function () {
                let re = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
                if(this.name==""||this.name==null){
                    alert("")
                }else if(this.password==""||this.password==""){
                    alert("")
                } else if(!re.test(this.email)){
                    alert("")
                }else if(!(/^1[34578]\d{9}$/.test(this.tel))){
                    alert("")
                }else{
                    localStorage.static="caihai";
                    localStorage.name=this.name;
                    localStorage.password=this.password;
                    localStorage.elail=this.email;
                    localStorage.tel=this.tel;
                    localStorage.balance=this.balance;
                    localStorage.min = this.min;
                    this.$confirm('0', '', {
                        distinguishCancelAndClose: true,
                        confirmButtonText: '',
                        cancelButtonText: ''
                    }).then(() => {
                        this.$router.push('/manage');
                    }).catch(() => {
                        this.$router.push('/');
                    })
                }
            }
        }
    }
</script>

<style scoped>
    .main{width: 100%;min-width: 1170px;margin:70px auto 90px;overflow: hidden;background: #ededed;padding:20px 0;position: relative;}
    .register{max-width: 1360px;height:400px;margin:0 auto;background-image: url("../assets/pic_Contact.jpg");background-size:780px 400px;background-repeat: no-repeat;overflow: hidden;}
    .from{width: 300px;margin-top: 100px;padding: 10px;margin-left: 780px;}
    .from h3{font-weight: normal;color: #343434;font-size: 22px;margin-bottom: 10px;}
    .el-input{margin-bottom: 10px;}
    .el-button{width: 100%;background: #0498cb;color: #fff;}
</style>